:root {
    --myblack: #191A19;
    --mydark: #393E46;
    --mylight: #AAD8D3;
    --mywhite: #EEEEEE;
    --mybtn: #00ADB5;
  }
.navbar{
    margin-top: 0;
    padding: 5px 60px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}
.navbar-dark{
    background-color: var(--myblack);
}
.brand{
  font-family: 'Ubuntu', sans-serif;
}
.brand-dark{
    color: var(--mywhite);
}
.navbar-light{
    background-color: var(--mylight);
}
.brand-light{
    color: var(--mydark);
}

/* switch */
.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}
  
/* Hide default HTML checkbox */
.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}
  
/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}
  
.slider:before {
  position: absolute;
  content: "";
  height: 20px;
  width: 20px;
  left: 1px;
  bottom: 1px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}
  
input:checked + .slider {
  background-color: #2196F3;
}
  
input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}
  
input:checked + .slider:before {
  -webkit-transform: translateX(20px);
  -ms-transform: translateX(20px);
  transform: translateX(20px);
}
  
/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}
  
.slider.round:before {
  border-radius: 50%;
}
